.main {
    min-height: 300px;
    display: flex;
    align-items: center;
    }

.title {
    font-size: 75px;
}

.subtitle {
    font-size: 25px;
    padding-top: 10px;
}

.welcome {
    width: 100%;
    text-align: center;
}

.buttonContainer{
    margin-top: 50px;
    display: flex;
 }

 .landingbutton{
    width:200px;
    height: 55px;
 }

 @media only screen and (min-width: 1200px) {
    .buttonContainer {
        padding: 0 200px;
    }
    .title {
        padding: 0 100px;
    }
 }
 @media only screen and (min-width: 768px) {
    .title {
        font-size: 60px;
    }
    .landingbutton {
        width: 150px;
        height: 50px;
    }
 }